*{
  margin: 0;
  padding: 0;
}

.logo {
  font-size: 30px;
  font-weight: 500;
  padding: 10px;
}

.box {

  margin: 30px;
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: 25px;
  font-weight: bold;
  border: 5px solid green;
  border-radius: 100px;
  padding: 10px;
}
.box h4 {
  font-size: 40px;
}
.box p {
  padding: 0;
  margin: 0;

}
.match-history {
    min-height: 50px;
    margin:20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.match-history .item {
  margin: 10px;
  border: 3px solid beige;
  min-width: 80%;
  min-height: 3rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.match-history .item h4 {
  width: fit-content;
  border: 2px solid yellow;
  padding: 3px;
  font-weight: 500;
  margin: 0px 10px;
}
.match-history .item p {
  border: 2px solid red;
  width: fit-content;
  margin: 0px 10px;
}
